	module.exports = {"data":{"markdownRemark":{"html":"<p>C73 It was Otsu &#x26; Happy New Year. I would like to express my sincere gratitude to those who purchased the danmaq work. If I think about it for the first nine years in a web site, three and a half years (officially 3 years) since my coterie activity started, it looks quite long and seems to be short. So thank you again this year.</p>\n<h2>What is this illustration?</h2>\n<p>The image is one of the skews drawn in Comiket (I really wanted to draw relationships with mice, but it was perfectly forgotten) orz As usual the line is not beautiful. I do not get accustomed to drawing one. Every time I draw one sheet at home, I've finished it by self repressing about 5 times on the light board, but I guess I'll try to practice with the lightboard sealed on this machine.</p>\n<h2>Slightly important notice</h2>\n<p>Sorry, I'm sorry, I will <em>pause danmaq activity for a while</em> . The period is about six months to one year. There are plenty of things left to do yet so I will come back later the following year. Thank you very much for taking care of everyone until now.</p>\n<h2>Activities that will continue even during inactivity</h2>\n<p><a href=\"http://e.danmaq.com/\">danmaq Personal Mail order \"e-danmaq\"</a></p>\n<ul>\n<li>Inventory production of past works (limited to handmade works such as CD-R · Copy books only)</li>\n<li>Update and maintain web site</li>\n<li>Extremely small individual creative activity to the extent that it is not far from him</li>\n</ul>","frontmatter":{"cover":null,"date":"2008-01-01T23:00:00+09:00","strDate":"2008/1/1","title":"I want to live loose this year"}}},"pathContext":{"path":"/en/blog/2008/01/01-今年はゆるーく生きたい/","langKey":"en"}}

